  transition: opacity 0.3s ease;
  user-select: none;
}
@media (hover: hover) {
  .header__logo-link:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.header__logo-link.focus-visible:focus {
  opacity: 0.8;
}
