    gap: 0;
  }
}
@media (max-width: 767px) {
  .header__list {
    margin: 0 10px 0 0;
    gap: 0;
  }
}

.header__link {
  display: flex;
  align-items: center;
  line-height: 18px;
  color: #f2f5f7;
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .header__link:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.header__link.focus-visible:focus {
  opacity: 0.8;
}
@media (max-width: 1023px) {
  .header__link {
    margin-left: 20px;
    padding: 10px;
  }
}

.header__link-text {
  margin-left: 15px;
}
@media (max-width: 1023px) {
  .header__link-text {
    display: none;
  }
}

.header__button {
  margin: 0 52px 7px 0;
  max-width: 200px;
  border: 1px solid #f2f5f7;
  border-radius: 3px;
}
@media (max-width: 1023px) {
  .header__button {
    display: none;
  }
}

