.advantages {
  padding-top: 120px;

  background-color: $color-magnolia;

  @include vp-1023 {
    padding-top: 80px;
  }

  @include vp-767 {
    padding-top: 50px;
  }
}

.advantages__list {
  display: grid;
  margin: 0 0 0 7px;
  padding: 0;

  list-style-type: none;

  gap: 30px;
  grid-template-columns: 1fr 1fr;

  @include vp-1023 {
    margin: 0;
  }

  @include vp-767 {
    display: block;
  }
}

.advantages__item {
  position: relative;

  min-height: 300px;
  padding: 40px 45px;

  border: 1px solid $color-whisper;

  @include vp-1023 {
    min-height: 360px;
    padding: 38px 45px 30px;
  }

  @include vp-767 {
    min-height: 325px;
    margin-bottom: 30px;
    padding: 30px;
  }

  &:first-of-type p {
    @include vp-767 {
      margin-bottom: 45px;
    }
  }

  p {
    margin: 0;
    padding: 0;

    @include vp-767 {
      margin-bottom: 25px;

      font-size: 14px;
      line-height: 19px;
    }
  }

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;

    width: 5px;
    height: 100%;

    background-color: $color-deep-sky-blue;
  }

  &:hover {
    color: $color-white;

    background-color: $color-deep-sky-blue;
  }

  &:hover svg {
    fill: $color-white;
  }

  &:hover h3 {
    color: $color-white;
  }

  &:last-of-type {
    @include vp-767 {
      margin-bottom: 0;
    }
  }
}

.advantages__icon {
  margin-bottom: 14px;

  fill: $color-deep-sky-blue;

  @include vp-1023 {
    margin-bottom: 7px;
  }

  @include vp-767 {
    width: 30px;
    height: 30px;
    margin-bottom: 9px;
  }
}

.advantages__title {
  margin: 0 0 14px;
  padding: 0;

  font-weight: 400;
  font-size: 24px;
  line-height: 34px;
  font-family: $ff-roboto-condensed;
  color: $color-deep-sky-blue;

  @include vp-1023 {
    margin: 0 0 7px;
  }

  @include vp-767 {
    margin: 0 0 11px;

    font-size: 20px;
    line-height: 140%;
  }
}
